:root{
  --default-gray: rgb(100, 100, 100);
  --tileWidth: 225px;
  --tileHeight: 140px;
  --main-radius: 5px;
  --main-padding: 15px;
}
.landing-container {
  font-family: Roboto;
}

.landing-content {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.landing-title {
  color: white;
  font-family: inherit;
  font-size: 68px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.landing-profiles {
  display: flex;
  /*
    flex-wrap: row wrap;
  justify-content: space-around;
  */
}
.landing-profile {
  display: flex;
  flex-direction: column;
  margin: 0px 25px;
  margin-top: 80px;
  margin-bottom: 80px;
}

.landing-profile:first-child {
  margin-left: 0px;
}
.landing-profile:last-child {
  margin-right: 0px;
}

.landing-profile:hover {
  cursor: pointer;
}

.landing-profile:hover .landing-display {
  color: rgb(220, 220, 220);
}

.landing-profile:hover .landing-image {
  border: 5px solid white;
}

.landing-profile .landing-image {
  margin-bottom: 15px;
  object-fit: cover;
  width: 200px;
  height: 200px;
  border: 5px solid transparent;
}

.landing-profile .landing-display {
  color: var(--default-gray);
  text-align: center;
  font-size: 22px;
}

@media only screen and (max-width: 650px) {
 
 
  .landing-title {
    text-align: center;
    font-size: 2em;
    padding: 10px;
    margin-top: -20px;
  }
  .landing-content {
   width: 100vw;
    height: 90vh;
    display: flex;
   
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .landing-profiles {
    width: 75vw;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content:space-evenly;
    padding: 30px;
  }

  .landing-profile {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
  
    margin: 10px 0px;
  }
  .landing-profile .landing-image {
    margin-bottom: 5px;
    object-fit: cover;
    width: 100px;
    height: 100px;
    border: 5px solid transparent;
  }

  .landing-profile .landing-display {
    color: var(--default-gray);
    text-align: center;
    font-size: 18px;
  }
}


body.active-modal {
  overflow-y: hidden;
}

.btn-modal {
  padding: 10px 20px;
  display: block;
  margin: 100px auto 0;
  font-size: 18px;
  border-radius: 10px;
}

.modal,
.overlay {
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: fixed;
}

.overlay {
  background: rgba(49, 49, 49, 0.8);
}
.modal-content {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  line-height: 1.4;
  background: rgba(255, 255, 255, 0.9);
  padding: 8px 8px;
  border-radius: 10px;
  max-width: 600px;
  min-width: 300px;
}

.dark-modal-content {
  position: absolute;
  top: 70%;
  left: 50%;
  transform: translate(-50%, -50%);
  line-height: 1.4;
  background: rgba(255, 255, 255, .95);
  padding: 8px 8px;
  border-radius: 10px;
  max-width: 600px;
  min-width: 300px;
}
.modal-content-test {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  line-height: 1.4;
  background: rgba(255, 255, 255, 0.9);
  padding: 8px 8px;
  border-radius: 10px;
  max-width: 600px;
  min-width: 300px;
}

.modal-content img {
  max-height: 100%;
  max-width: 100%;
  object-fit: cover;
  overflow: hidden;
}

.close-modal {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 5px 7px;
  border-radius: 15px;
}

.fate-total {
  text-align: center;
  margin-bottom: 10%;

}
.fate-1 {
  font-size: 1.25rem;
  text-shadow: 1px 1px rgb(0, 0, 0);
  color:rgb(136, 135, 135);
}

.message-1 {
  font-size: 1.15rem;
}

.disclaimer {
  font-size: .75rem;
  text-shadow: .75px .75px gray;
  text-align: right;
}

