.list-container {
  width: 100%;
  margin-top: 10px;
  margin-left: 20px;
  padding-right: 30px;

}

.list-container > .list-wrapper::-webkit-scrollbar{
  display: none;
}

.list-container > .list-wrapper {
  position: relative;
  overflow-y: hidden;
  overflow-x: scroll;
  margin-bottom: 15px;
}

.list-container > .list-title {
  color: white;
  font-size: 18px;
  font-weight: 400;
  margin-left: 20px;
  margin-bottom: 5px;
}

.list-container > .list-wrapper > .list-slider-arrow-right {
  width: 50px;
  height: 100%;
  color: rgb(255, 255, 255);
  background-color: rgb(15, 15, 15, 0.5);
  position: absolute;
  z-index: 99;
  right: 0;
  top: 0;
  bottom: 0;
  cursor: pointer;
  /*margin: auto;*/
}

.list-container > .list-wrapper > .list-slider-arrow-left {
  width: 50px;
  height: 100%;
  color: white;
  background-color: rgb(15, 15, 15, 0.5);
  position: absolute;
  z-index: 99;
  left: 0;
  top: 0;
  bottom: 0;
  cursor: pointer;
  /*margin: auto;*/
}
.list-container > .list-wrapper > .list-container {
  display: flex;
  width: -webkit-max-content;
  width: max-content;
  margin-left: 50px;
  transform: translateX(0px);
  transition: all 0.5s ease;
  padding-top: 15px;
  
}

@media screen and (max-width: 1020px) {
  .list-container > .list-wrapper > .list-slider-arrow-left {
    display: none;
  }
  .list-container > .list-wrapper > .list-slider-arrow-right {
    display: none;
  }
}

.list-item {
  width: var(--tileWidth);
  height: var(--tileHeight);

  background-color: #2f4353;
  background-image: linear-gradient(90deg, #2f4353 0%, #d2ccc4 94%);
  margin-right: 10px;
  overflow: hidden;
  position: relative;
  margin-top: 15px;
  transition: all 0.3s ease-in-out;
}

.list-item > img {
  object-fit: cover;
  width: 97%;
  height: 140%;
  border: 5px solid transparent;
}

.list-item:hover {
  width: 245px;
  height: 260px;
  border: 5px solid white;
  margin-top: -15px; 
}

.list-item:hover > img {
  height: 210px;
  transition: all 0.3s ease; 
}

.list-item > .list-item-characterTitle {
  position: absolute;
  top: 6px;
  left: 10px;
  color: rgb(255, 255, 255);
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  font-size: 22px;
  text-shadow: 1px 1px 0 rgb(180, 180, 180);
}

.list-item-characterInfo {
  display: flex;

  padding: 5px;
  justify-content: space-between;
  color: white;
  font-size: 20px;
  font-family: "Roboto";
  font-weight: 400;
}

.list-item-positionInfo {
  display: flex;
  margin-bottom: 5px;
}
.list-item-position1,
.list-item-position2,
.list-item-position3 {
  border-radius: 100%;
  width: 17px;
  height: 17px;
  text-align: center;
  font-size: 14px;
  padding: 4px;
  border: 2px solid white;
  background-color: var(--default-gray);
}

