.details-container {
  background: rgb(30, 30, 30);
  color: white;
  display: grid;
  min-height: 100vh;

  grid-gap: 0.3rem;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 0.2fr 0.002fr 1fr 1fr 1fr 0.25fr;

  grid-template-areas:
    "title title title"
    "titleInfo titleInfo titleInfo"
    "positionInfoBlock main main"
    "positionInfoBlock main main"
    "positionInfoBlock main main"
    "footer footer footer";
}

